}


.box {
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    margin:70px auto;
    max-width: 600px;
    width: 100%;
    min-height: 250px;
    padding: 20px;
    background: #FFFFFF;
    text-align: center;
    transition: 0.25s;
    border-radius: 5px;
    border: 1px solid #77787B;
}


